</template>

<script type="text/javascript">
	export default{
		name : 'InputModal',
		mounted(){
			document.getElementById('kode').addEventListener("keyup", function(e) {
				if(e.target.value > 79){
					this.value = 79;
				}
			});
			document.getElementById('kode').addEventListener("blur", function(e) {
				if(e.target.value < 71){
					this.value = 71;
				}
			});
			var self = this;
			$("#kode").on("change keyup blur", function(){
				self.generateCoa(1);
			});
			$("#identitas").on("change keyup blur", function(){
				self.generateCoa(1);
			});
			$("#no_urut").on("change keyup blur", function(){
				self.generateCoa(1);
			});
			$("#slot").on("change keyup blur", function(){
				self.generateCoa(1);
			});


			document.getElementById('kode2').addEventListener("keyup", function(e) {
				if(e.target.value > 79){
					this.value = 79;
				}
			});
			document.getElementById('kode2').addEventListener("blur", function(e) {
				if(e.target.value < 71){
					this.value = 71;
				}
			});
			$("#kode2").on("change keyup blur", function(){
				self.generateCoa(2);
			});
			$("#identitas2").on("change keyup blur", function(){
				self.generateCoa(2);
			});
			$("#no_urut2").on("change keyup blur", function(){
				self.generateCoa(2);
			});
			$("#slot2").on("change keyup blur", function(){
				self.generateCoa(2);
			});
		},
		methods : {
			generateCoa(v){
				if(v == 1){
					var kode = $("#kode").val();
					var identitas = $("#identitas").val();
					var no_urut = $("#no_urut").val();
					var slot = $("#slot").val();
					var keluaran = $("#keluaran");
				}else if(v == 2){
					var kode = $("#kode2").val();
					var identitas = $("#identitas2").val();
					var no_urut = $("#no_urut2").val();
					var slot = $("#slot2").val();
					var keluaran = $("#keluaran2");
				}

				var coa = this.validateCoa(kode)+this.validateCoa(identitas)+this.validateCoa(no_urut)+this.validateCoa(slot);
				keluaran.val(coa.slice(0,-1));
			},
			validateCoa(val){
				if(val == ""){
					return "";
				}
				if(val.length == 1){
					return "0."+val+".";
				}else{
					var ret = "";
					for(var i = 0; i < val.length; i++){
						ret += val.charAt(i)+".";
					}
					return ret;
				}
			}
		}
	}
</script>